<template>
     <div>
          <button @click="increaseCounter">Add 1</button>
          <button @click="decreaseCounter">Sub 1</button>
          <p>{{ counter }}</p>
     </div>
</template>
<script>
    export default {
        components:{},
        data(){
            return{
                 counter: 0,
                 list: [],
            }
        },
        setup(){},
        created(){},
        unmounted(){},
        methods:{
            increaseCounter(){
                this.counter += 1
            },
            decreaseCounter(){
                this.counter -= 1
            }
        }
    }
</script>

