<template>
     <div>
        <input type="text" v-bind:value="userId" readonly>
        <input type="text" :value="userId" readonly>
        <!-- v-bind:value 와 :value 는 같은 표현임. v-bind는 생략이 가능함. -->
        <br>
        <img :src="imgSrc" alt="" style="width:100px; height:auto">
        <!-- :src 는 어트리뷰트에 데이터를 바인드한 것 -->
        <br>
        <input type="search" name="" id="" v-model="search_txt">
        <button :disabled="search_txt ===''">조회</button>
     </div>
</template>
<script>
    export default {
        components:{},
        data(){
            return{
                userId: 'ccnplaza',
                imgSrc: 'https://upload.wikimedia.org/wikipedia/commons/f/f1/Vue.png',
                search_txt: ''
            }
        },
        setup(){},
        created(){},
        mounted(){},
        unmounted(){},
        methods:{}
    }
</script>

