<template>
     <div>
         <!-- input 필드의 키업 이벤트로 함수호출하는 방식 -->
         <input type="text" @keyup="checkEnter($event)" v-model="searchText">
         <!-- input 필드의 키업 이벤트로 함수호출하는 방식2 -->
          <input type="text" @keyup.enter="doSearch" v-model="searchText">
          <button @click="doSearch">조회</button>
     </div>
</template>
<script>
    export default {
        components:{},
        data(){
            return{
                 sampleData: ''
            }
        },
        setup(){},
        created(){},
        mounted(){},
        unmounted(){},
        methods:{
            doSearch(){
                console.log(this.searchText)
            },
            checkEnter(event){
                if(event.keyCode === 13){
                    this.doSearch();
                }
            }
        }
    }
</script>

